<template>
    <div>
        <v-row justify="center" align="center">
            <v-col cols="12" md="10" sm="12">
                <v-btn outlined block x-large color="primary" @click.stop="previousStep" v-if="currentStep==2">
                    <v-icon class="mr-2">arrow_back</v-icon>Previous Step
                </v-btn>
                <v-stepper v-model="currentStep">
                    <v-stepper-header>
                        <v-stepper-step :complete="currentStep > 1" step="1">User Information</v-stepper-step>
                        <v-divider></v-divider>
                        <v-stepper-step step="2">Delivery Information</v-stepper-step>
                    </v-stepper-header>
                    <v-stepper-items>
                        <v-stepper-content step="1">
                            <UserInfoForm
                            :userDetails="userInfo"
                            :error="userInfoError"
                            :errorMessage="userInfoErrorMessage"
                            @valid-user-info="nextStep"
                            ></UserInfoForm>
                        </v-stepper-content>
                        <v-stepper-content step="2">
                            <DeliveryInfoForm
                            :deliveryAddress="deliveryInfo"
                            :error="error"
                            :errorMessage="errorMessage"
                            :onRegister="updateDetails"
                            ></DeliveryInfoForm>
                        </v-stepper-content>
                    </v-stepper-items>
                </v-stepper>
            </v-col>    
        </v-row>
    </div>
</template>
<script>
import UserInfoForm from "@/components/auth/UserInfoForm.vue"
import DeliveryInfoForm from "@/components/auth/DeliveryInfoForm.vue"
import {mapGetter, mapGetters, mapActions} from "vuex"
export default {
    name:"ChangeUserDetails",
    components:{
        UserInfoForm,
        DeliveryInfoForm
    },
    data:()=>{
        return{
           userInfoError:'',
            error:'',
            errorMessage :'',
            userInfoErrorMessage:'',
            currentStep: 1
        }
    },
    computed:{
        ...mapGetters(["retrieveUser", "retrieveCustomerAddress"]),
        userInfo(){
            return{
                userName: this.retrieveCustomerAddress.name,
                email:this.retrieveUser.email_address,
                telephone : this.retrieveCustomerAddress.telephone_no
            }
        },
        deliveryInfo(){
            return{
                addressLine1:this.retrieveCustomerAddress.address_line_1,
                addressLine2 :this.retrieveCustomerAddress.address_line_2,
                addressLine3 :this.retrieveCustomerAddress.address_line_3,
                addressLine4 :this.retrieveCustomerAddress.address_line_4,
                postcode :this.retrieveCustomerAddress.postcode
            }
        }
    },
    methods:{
        ...mapActions(["updateUserDetails","getCustomer", "setSnackbar"]),
        nextStep() {
            this.currentStep = this.currentStep + 1;
        },
        previousStep() {
            this.currentStep = this.currentStep - 1;
        },
        resetForm(){
            this.userInfo.userName =''
            this.userInfo.email = ''
            this.userInfo.telephone = ''
            this.deliveryInfo.addressLine1 = ''
            this.deliveryInfo.addressLine2 = ''
            this.deliveryInfo.addressLine3 = ''
            this.deliveryInfo.addressLine4 = ''
            this.deliveryInfo.postcode = ''
        },
       updateDetails(){
           //this.$store.dispatch('updateUserDetails',{pay:this.userInfo})
           this.error=false
           this.errorMessage=''
            const rec = Object.assign(
                {},
                {userId: this.retrieveUser.userNum},
                {sessionKey: this.retrieveUser.sessionKey},
                {userName: this.userInfo.userName},
                {email_address : this.userInfo.email},
                {telephone : this.userInfo.telephone},
                {address_line_1 : this.deliveryInfo.addressLine1},
                {address_line_2 : this.deliveryInfo.addressLine2},
                {address_line_3 : this.deliveryInfo.addressLine3},
                {address_line_4 : this.deliveryInfo.addressLine4},
                {postcode : this.deliveryInfo.postcode},
            );
           this.updateUserDetails(rec).then((response)=>{
               if(response.success ==true){
                   console.log('Fetch Customer Now')
                   this.getCustomer().then((response)=>{
                       if(response.success == true){
                            console.log('Redirect to Account Page')
                            this.previousStep()
                            this.resetForm()
                            this.setSnackbar({text:'Details Updated Successfully'})
                            this.$router.push({name:'UserAccount'})
                       }else{
                           this.setSnackbar({text:'Error Loading Customer', color:'error'})
                           console.log('Error Loadig Customer')
                       }
                   }).catch((er)=>{
                       console.log(er)
                   })  
               }else{
                   this.error=true
                   this.errorMessage = response.messages[0]
                   this.setSnackbar({text:response.messages[0], color:'error'})
                   console.log('Error Updating User')
               }
           }).catch((er)=>{
               console.log(er)
           })
       }
    }
}
</script>