<template>
  <v-row justify="center" align="start">
    <v-col cols="12" lg="12" md="12" xs="12">
      <v-btn outlined block color="blue darken-2" exact @click.prevent="handleBack('/')">Back</v-btn>
    </v-col>
    <v-col cols="12" lg="3" md="12" xs="12">
      <ProductImage :product="product"></ProductImage>
    </v-col>
    <v-col cols="12" md="9" xs="12">
      <v-row justify="start" align="start">
        <v-col cols="12" xs="12" class="pt-0">
          <ProductName :product="product"></ProductName>
        </v-col>
        <v-col cols="12" xs="12">
          <div class="subtitle-2">Description</div>
          <div>{{longDescription}}</div>
        </v-col>

        <v-col cols="12" xs="12">
          <div class="subtitle-2">Allergy Information</div>
          <ProductAllergen :product="productAllergens"></ProductAllergen>
        </v-col>

        <v-col cols="12" xs="12">
          <div class="subtitle-2">Nutritional Information</div>
          <ProductNutrition :product="prodNutrition"></ProductNutrition>
        </v-col>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
import ProductImage from "@/components/product/ProductImage.vue";
import ProductName from "@/components/product/ProductName.vue";
import ProductAllergen from "@/components/product/ProductAllergen.vue" 
import ProductNutrition from "@/components/product/ProductNutrition.vue" 
import {mapActions , mapGetters} from "vuex"

export default {
  components: {
    ProductImage,
    ProductName,
    ProductAllergen,
    ProductNutrition
  },

  props: {
    product: {}
  },

  data() {
    return {
      fromRoute: null,
      productAllergens:{},
      prodNutrition:{}
    };
  },

  activated() {
    console.log("-- Activated Product");
    console.log(this.product);
    this.getProductAllergens()
    this.getProductNutrition()
  },

  computed: {
    ...mapGetters(["retrieveUser"]),
    longDescription() {
      if (this.product.long_description != "") {
        return this.product.long_description;
      }

      return "No product description available";
    },
    productNumber(){
      if(this.product){
        return this.product.prod_no
      }
    },
    rec(){
      return Object.assign(
              {},
              {userId: this.retrieveUser.userNum},
              {sessionKey: this.retrieveUser.sessionKey},
              {prod_no : this.productNumber},
          );
    }
  },

  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fromRoute = from;
    });
  },

  methods: {
    ...mapActions(["fetchProductAllergens","fetchProductNutritions"]),

    getProductAllergens(){
      this.fetchProductAllergens(this.rec)
      .then((response)=>{
          if(response.success==true){
              this.productAllergens= response.allergens
              console.log('Populating Allergens...')
          }else{
              console.log('Error Fetching Allergen Report')
          }
      }).catch((er)=>{
          console.log(er)
      })

    }, 
    getProductNutrition(){
        this.fetchProductNutritions(this.rec)
        .then((response)=>{
            if(response.success==true){
                this.prodNutrition= response.nutrition
                console.log('Populating Nutrients...')
            }else{
                console.log('Error Fetching Allergen Report')
            }
        }).catch((er)=>{
            console.log(er)
      })
    },
    /**
     * Handle Back
     * @desc Extends default router back functionality
     * @param {string} fallback - The fallback path if there is no history to use with $router.back(). This is usually the case if the page was visited directly or from another site
     **/
    handleBack(fallback) {
      if (!this.fromRoute.name) {
        this.$router.push(fallback);
      } else {
        this.$router.back();
      }
    },
    
    
    
  }, 

};
</script>

