<template>
    <div>
        <v-row justify="center" align="center">
            <v-col cols="12" md="10" sm="12">
                <v-banner icon="list_alt" single-line>Your Orders</v-banner>
                <v-btn block x-large @click.stop="fetchExistingOrders">
                <v-icon class="mr-2">refresh</v-icon>Refresh
                </v-btn>
                <v-toolbar class="elevation-1">
                    <v-toolbar-title class="subtitle-2 font-weight-medium">
                        Deliver Date : {{getDeliveryDate()}}
                    </v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-toolbar-title class="subtitle-2 font-weight-medium">
                        Order Ref : {{orderRef}}
                    </v-toolbar-title>
                </v-toolbar>
                <v-data-table :headers="headers" :items="tickets" hide-default-footer class="elevation-1">
                    <template v-slot:item.total="{item}">
                        <span>
                        <span class="font-weight-medium">£ {{item.net_total}}</span>
                        </span>
                    </template>
                    <template v-slot:item.actions="{item}">
                        <v-tooltip right>
                            <template v-slot:activator="{ on }">
                                <v-btn icon color="primary" v-on="on" @click.stop="goToDetailsPage(item)">
                                <v-icon>info</v-icon>
                                </v-btn>
                            </template>
                            <span>More Information</span>
                        </v-tooltip>
                    </template>
                </v-data-table>
            </v-col>
        </v-row>
    </div>  
</template>
<script>
export default {
    name:"OrderDetails",
    data:()=>{
        return{
            headers : [
                { text: "Distributor Name", value: "distributor_name" },
                { text: "Invoice No", value: "invoice_no", sortable: false },
                { text: "Invoice Total", value: "total", sortable: false },
                { text: "Actions", value: "actions", sortable: false }
            ]
        }
    },
    computed:{
        tickets(){
            return this.$store.state.ordersHistory.orderTickets
        },
        orderRef(){
            return this.$route.params.td_id
        }
    },
    methods:{
        getDeliveryDate(){
            let order = this.$store.state.ordersHistory.orders.find(order =>(order.td_id === this.orderRef))
            let deliveryDate = ""
            if(order){
                deliveryDate = order.transaction_date
            }
            return deliveryDate
        },
        goToDetailsPage(order){
            this.getOrderProducts(order)
            this.$router.push({name:'TicketDetails',params:{td_id:order.tdd_id, invoice_no:order.invoice_no}})
        },
        getOrderProducts(ord){
            this.$store.dispatch('fetchOrdersProducts',{tdId : ord.tdd_id}).then((response)=>{
            if(response == true){
                console.log('Products Found And Retrieved')
            }else{
                console.log('Failed to retreive Order Products')
            }
            }).catch((er)=>{
                console.log(er);
            })
        },
    }
}
</script>