<template>
  <v-alert v-if="!validMandatory" prominent type="warning" class="ma-0 px-4 py-2" outlined text>
    <v-row align="center">
      <v-col
        class="grow"
      >You must have at least one item from the category {{mandatoryInfo.category_desc}} to check out</v-col>
      <v-col class="shrink">
        <v-btn
          v-if="this.$route.name != 'OrdersSearch'"
          color="warning"
          :to="{name:'OrdersSearch'}"
        >Search</v-btn>
        <v-btn v-else color="warning" @click="$emit('getMandatoryCat')">Load</v-btn>
      </v-col>
    </v-row>
  </v-alert>
</template>

<script>
export default {
  computed: {
    validMandatory() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return this.$store.getters.retrieveCurrentTrolly.mandatory_cat.valid;
      }

      return true;
    },
    mandatoryInfo() {
      return this.$store.getters.retrieveCurrentTrolly.mandatory_cat;
    }
  }
};
</script>

