<template>
  <v-dialog ref="dialog" v-model="modal" persistent width="290px">
    <template v-slot:activator="{ on }">
      <v-text-field
        :disabled="checkDatePickerDisabled"
        outlined
        v-model="computedDateFormattedMomentjs"
        :rules="dateRules"
        label="Order Date"
        prepend-inner-icon="event"
        readonly
        v-on="on"
      ></v-text-field>

      <v-alert
        v-if="checkDatePickerDisabled"
        type="warning"
        border="left"
      >Unfortunately no dates are available at this time.</v-alert>
    </template>
    <v-date-picker
      v-model="orderDate"
      no-title
      :allowed-dates="getAllowedDates"
      v-on:change="$emit('input', orderDate), modal= false"
    >
      <v-spacer></v-spacer>
      <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
      <!-- <v-btn text color="primary" @click="$refs.dialog.save(orderDate)">OK</v-btn> -->
    </v-date-picker>
  </v-dialog>
</template>

<script>
import moment from "moment";

export default {
  components: {},

  data() {
    return {
      orderDate: this.getInitialDate(),
      modal: false,
      dateRules: [v => !!v || "Order Date is required"]
    };
  },
  computed: {
    computedDateFormattedMomentjs() {
      return this.orderDate
        ? moment(this.orderDate).format("dddd, Do MMMM YYYY")
        : "";
    },
    checkDatePickerDisabled() {
      let datesDisabled = true;

      if (this.$store.getters.retrieveAllAvailableDays.length > 0) {
        datesDisabled = false;
      }

      return datesDisabled;
    }
  },
  methods: {
    getInitialDate() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return this.$store.getters.retrieveCurrentTrolly.order_date;
      } else {
        return null;
      }
    },
    getAllowedDates(val) {
      if (this.$store.getters.retrieveAllAvailableDays.indexOf(val) !== -1) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>