<template>
    <div>
        <v-row justify="center" align="center">
            <v-col cols="12" md="10" sm="12">
                <v-banner icon="list_alt" single-line>Your Orders</v-banner>
                <v-btn block x-large @click.stop="fetchOrdersProducts({tdId:orderRef})">
                    <v-icon class="mr-2">refresh</v-icon>Refresh
                </v-btn>
                <v-toolbar class="elevation-1">
                    <v-toolbar-title class="subtitle-2 font-weight-medium">
                        Distributor : {{getDistributorName()}}
                    </v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-toolbar-title class="subtitle-2 font-weight-medium">
                        Invoice No : {{invoiceNo}}
                    </v-toolbar-title>
                </v-toolbar>
                <v-data-table :headers="headers" :items="orderProducts" hide-default-footer class="elevation-1">
                    quantity_ordered
                    <template v-slot:item.qty="{item}">
                        <span>
                            {{parseFloat(item.quantity_ordered).toFixed(0)}}
                        </span>
                    </template>
                    <template v-slot:item.price="{item}">
                        <span>
                            <span class="font-weight-medium">£ {{parseFloat(item.item_price).toFixed(2)}}</span>
                        </span>
                    </template>
                    <template v-slot:item.value="{item}">
                        <span>
                            <span class="font-weight-medium">£ {{parseFloat(item.item_value).toFixed(2)}}</span>
                        </span>
                    </template>
                </v-data-table>   
                <v-card class="elevation-1">
                    <v-card-title class="subtitle-2 justify-center">
                    Invoice Summary
                    </v-card-title>
                    <v-list-item two-line>
                        <v-row>
                            <v-col cols="6" sm="3" md="3" lg="2" >
                                <v-list-item-content>
                                    <v-list-item-title class="subtitle-2 font-weight-medium">Total Packages</v-list-item-title>
                                    <v-list-item-subtitle v-if="retrieveInvoiceSummary">{{retrieveInvoiceSummary.totalPackages}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-col>
                            <v-col cols="6" sm="3" md="3" lg="2">
                                <v-list-item-content>
                                    <v-list-item-title class="subtitle-2 font-weight-medium">Total Boxes</v-list-item-title>
                                    <v-list-item-subtitle v-if="retrieveInvoiceSummary">{{retrieveInvoiceSummary.totalBox}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-col>
                            <v-col cols="6" sm="3" md="3" lg="2">
                                <v-list-item-content>
                                    <v-list-item-title class="subtitle-2 font-weight-medium">Total Kilo</v-list-item-title>
                                    <v-list-item-subtitle v-if="retrieveInvoiceSummary">{{retrieveInvoiceSummary.totalKilos}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-col>
                            <v-col cols="6" sm="3" md="3" lg="2">
                                <v-list-item-content>
                                    <v-list-item-title class="subtitle-2 font-weight-medium">Total Each</v-list-item-title>
                                    <v-list-item-subtitle v-if="retrieveInvoiceSummary">{{retrieveInvoiceSummary.totalEach}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-col>
                            <v-col cols="6" sm="3" md="3" lg="2">
                                <v-list-item-content>
                                    <v-list-item-title class="subtitle-2 font-weight-medium">Total Value</v-list-item-title>
                                    <v-list-item-subtitle v-if="retrieveInvoiceSummary">£ {{parseFloat(retrieveInvoiceSummary.totalValue).toFixed(2)}}</v-list-item-subtitle>
                                </v-list-item-content>
                            </v-col>
                        </v-row>
                    </v-list-item>
                </v-card> 
            </v-col>
        </v-row>
    </div>  
</template>
<script>
import { mapActions, mapGetters } from "vuex";
export default {
    name : "TicketDetails",
    data:()=>{
        return{
            headers :[
                { text: "Product", value: "product_no" },
                { text: "Description", value: "prod_desc", sortable: false },
                { text: "Variety", value: "variety", sortable: false },
                { text: "Size", value: "size", sortable: false },
                { text: "Type", value: "quantity_type", sortable: false },
                { text: "Qty", value: "qty", sortable: false },
                { text: "Price", value: "price", sortable: false },
                { text: "Value", value: "value", sortable: false },
            ],
           
           totalEach:0

        }
    },
    computed:{
        ...mapGetters(["retrieveAllOrders","retrieveInvoiceSummary"]),
        orderProducts(){
            return this.$store.state.ordersHistory.orderProducts
        },
        
        tdId(){
            return this.$route.params.td_id
        },
        invoiceNo(){
            return this.$route.params.invoice_no
        },
        orderRef(){
            return this.$route.params.td_id
        }
        
        
    },
    methods:{
        ...mapActions(["fetchOrdersProducts"]),
        getDistributorName(){
            let orderItem = this.$store.state.ordersHistory.orders.find(orderNo=> orderNo.td_id === this.tdId)
           let distName = "";
           if(orderItem){
               distName = orderItem.tickets[this.invoiceNo].distributor_name;
           }
           return distName;
        }
    },
    created(){
        
    },
    beforeDestroy(){
    }
}
</script>