<template>
  <v-flex lg2 md3 sm4 xs6 class="mb-4">
    <v-card
      class="mx-auto"
      flat
      hover
      :to="{name: 'OrdersSearchProduct',  params: { id: product.prod_no, product: product}}"
      max-width="200px"
      height="100%"
    >
      <v-row justify="start" align="stretch" no-gutters style="
    height: 100%;
">
        <v-col cols="12" lg="12" xs="12">
          <ProductImage :product="product"></ProductImage>
        </v-col>
        <v-col cols="12" lg="12" xs="12">
          <v-row justify="start" align="start" no-gutters>
            <v-col cols="12" lg="12" xs="12">
              <v-card-title class="py-2 px-1">{{productName}}</v-card-title>

              <v-card-subtitle class="pa-1">
                <div>{{productVarSize}}</div>
                <div v-if="showCode">
                  <span>
                    Code:
                    <strong>{{product.prod_no}}</strong>
                  </span>
                </div>
                <div v-if="product.country_of_origin != null && showCountry">
                  <span>
                    Country:
                    <strong>{{product.country_of_origin}}</strong>
                  </span>
                </div>
              </v-card-subtitle>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12" lg="12" xs="12">
          <PriceDisplay :product="product"></PriceDisplay>
        </v-col>
      </v-row>
    </v-card>
  </v-flex>
</template>

<script>
import ProductImage from "@/components/product/ProductImage.vue";
import PriceDisplay from "@/components/product/PriceDisplay.vue";

export default {
  components: {
    ProductImage,
    PriceDisplay
  },
  props: {
    product: {}
  },

  computed: {
    isProdGrid() {
      if (this.$store.getters.retrieveProductGrid) {
        return true;
      } else {
        return false;
      }
    },
    showCode() {
      return this.$store.getters.retrieveProductCode;
    },
    showCountry() {
      return this.$store.getters.retrieveProductCountry;
    },
    productName() {
      if (
        (this.$store.getters.retrieveIsCncCustomer &&
          this.product.CsAndCncProductDescription != null) ||
        (this.$store.getters.retrieveIsCashCustomer &&
          this.product.CsAndCncProductDescription != null)
      ) {
        return this.product.CsAndCncProductDescription;
      } else {
        return this.product.prod_desc;
      }
    },
    productVarSize() {
      if (
        this.$store.getters.retrieveIsCncCustomer ||
        (this.$store.getters.retrieveIsCashCustomer &&
          this.product.CsAndCncProductDescription != null)
      ) {
        return null;
      } else {
        let returnString = "";

        if (this.product.variety) {
          returnString += this.product.variety;
        }

        if (this.product.size) {
          if (returnString != "") {
            returnString += " - ";
            returnString += this.product.size;
          }
        }

        return returnString;
      }
    }
  }
};
</script>

