<template>
  <v-alert v-if="showCountdown" 
    type="warning" outlined text
  >Time Remaining to order for Selected Delivery Date 
    <span class="font-weight-bold red--text">{{countdownTime}}</span>  minutes
  </v-alert>
</template>

<script>
import moment from "moment";
import {mapGetters} from "vuex"
export default {
  name:"CutOff",
  computed: {
    ...mapGetters(["retrieveCutOffTimeWarning","retrieveCurrentTrolly"]),
    showCountdown() {
      if(this.retrieveCutOffTimeWarning >= this.remainingWarningMinutes){
        return true
      }else{
        return false
      }
    },
    remainingWarningMinutes(){
      if(this.retrieveCurrentTrolly){
        return this.retrieveCurrentTrolly.mins_before_cutoff_time
      }
      
    },

    countdownTime() {
      let remainingMinutes =this.remainingWarningMinutes
      return moment.utc(moment.duration(remainingMinutes*60,"seconds").asMilliseconds()).format("HH:mm")
    }
  },

  methods: {
  }
};
</script>

