<template>
  <v-flex md12 xs12>
    <v-card outlined>
      <v-alert text type="error" class="ma-0" v-if="!product.valid">Error: {{product.message}}</v-alert>

      <v-layout row wrap justify-start align-center style="margin-top: 1px">
        <v-flex md2 xs4>
          <v-layout row wrap justify-start align-center>
            <v-flex md12 py-0>
              <ProductImage :product="product"></ProductImage>
            </v-flex>
          </v-layout>
        </v-flex>

        <v-flex md10 xs8>
          <v-card flat color="background">
            <v-layout row wrap justify-center align-center>
              <v-flex fill-height md12 xs12>
                <v-layout row wrap justify-space-between align-start>
                  <v-flex fill-height md4 xs6>
                    <ProductName :product="product"></ProductName>
                  </v-flex>

                  <v-flex fill-height md5 xs6>
                    <div class="py-2 px-1 subtitle-2">{{product.itemType}}</div>
                    <div class="py-0 px-1 body-2">
                      Quantity:
                      <span class="subtitle-2">{{productDisplayQty}}</span>
                    </div>
                    <div class="py-0 px-1 body-2">
                      Price:
                      <span class="subtitle-2">£{{itemPrice}}</span>
                    </div>
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-layout>
            <v-card-actions class="hidden-sm-and-down">
              <v-btn color="error" text @click="$emit('deleteTrollyItem', product)">Delete</v-btn>

              <v-spacer></v-spacer>
              <v-btn
                color="deep-purple base"
                class="white--text"
                @click="$emit('selectProduct', product)"
              >Edit</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
      <v-card-actions class="hidden-md-and-up">
        <v-btn color="error" text @click="$emit('deleteTrollyItem', product)">Delete</v-btn>

        <v-spacer></v-spacer>
        <v-btn
          color="deep-purple base"
          class="white--text"
          @click="$emit('selectProduct', product)"
        >Edit</v-btn>
      </v-card-actions>
    </v-card>
  </v-flex>
</template>

<script>
import { mapActions } from "vuex";

import ProductImage from "@/components/product/ProductImage.vue";
import ProductName from "@/components/product/ProductName.vue";

export default {
  components: {
    ProductImage,
    ProductName
  },
  props: {
    product: {}
  },
  data() {
    return {};
  },
  computed: {
    itemPrice() {
      switch (this.product.qty_type) {
        case "E":
          if (
            !this.$store.getters.retrieveIsCncCustomer &&
            !this.$store.getters.retrieveIsCashCustomer &&
            parseInt(this.product.catch_weight_flag) == 2
          ) {
            return parseFloat(this.product.kilo_price).toFixed(2) + "/kg";
          } else {
            return parseFloat(this.product.itemPrice).toFixed(2);
          }

        default:
          return parseFloat(this.product.itemPrice).toFixed(2);
      }
    },

    isProdGrid() {
      if (this.$store.getters.retrieveProductGrid) {
        return true;
      } else {
        return false;
      }
    },
    showCode() {
      return this.$store.getters.retrieveProductCode;
    },
    showCountry() {
      return this.$store.getters.retrieveProductCountry;
    },
    productDisplayQty() {
      return parseFloat(this.product.qty)
        .toFixed(2)
        .replace(/\.0+$/, "");
    },
    productVarSize() {
      if (
        this.$store.getters.retrieveIsCncCustomer ||
        (this.$store.getters.retrieveIsCashCustomer &&
          this.product.CsAndCncProductDescription != null)
      ) {
        return null;
      } else {
        let returnString = "";

        if (this.product.variety) {
          returnString += this.product.variety;
        }

        if (this.product.size) {
          if (returnString != "") {
            returnString += " - ";
            returnString += this.product.size;
          }
        }

        return returnString;
      }
    }
  },

  methods: {
    ...mapActions(["getTrollys"])
  }
};
</script>

