<template>
  <v-row justify="start" align="start">
    <v-col
      v-if="this.$store.getters.retrieveIsCashCustomer || this.$store.getters.retrieveIsCncCustomer"
      cols="12"
      md="12"
      sm="12"
    >
      <CncSwitch></CncSwitch>
      <CncAddress class="mt-4"></CncAddress>
    </v-col>
    <v-col cols="12" md="12" sm="12">
      <v-alert
        v-if="this.$store.getters.retrieveIsCashCustomer"
        type="info"
        class="ma-0"
        outlined
        text
      >This is a delivery order.</v-alert>
      <v-alert
        v-if="this.$store.getters.retrieveIsCncCustomer"
        type="info"
        class="ma-0"
        outlined
        text
      >This is a collection order.</v-alert>

      <MandatoryCategory class="mt-4"></MandatoryCategory>
      <CutOff class="mt-4"></CutOff>
      <InvalidDate class="mt-4"></InvalidDate>
    </v-col>

    <v-col cols="12" lg="8" sm="12">
      <v-row justify="space-between" align="center">
        <v-col v-if="totalBasketItems" cols="12" lg="12" sm="12">
          <div class="headline">Your Basket</div>
        </v-col>

        <v-col v-if="totalBasketItems" cols="12" lg="12" sm="12">
          <MinimumValue></MinimumValue>
        </v-col>

        <v-col v-if="totalBasketItems" cols="12" lg="4" sm="12">
          <v-row justify="space-between" align="center">
            <v-col v-if="totalBasketItems" cols="12" lg="6" sm="12">
              <div class="subtitle-2">No. Items: {{totalBasketItems}}</div>
            </v-col>
            <v-col v-if="totalBasketItems" cols="12" lg="6" sm="12">
              <div class="subtitle-2">Total Quantity: {{totalQty}}</div>
            </v-col>
          </v-row>
        </v-col>

        <v-col v-if="totalBasketItems" cols="12" lg="3" sm="12">
          <div class="subtitle-2" style="text-align: end;">Total Value: £{{basketTotal}}</div>
        </v-col>

        <v-col v-if="!totalBasketItems" cols="12" lg="12" sm="12">
          <div class="headline">Your basket is empty</div>
        </v-col>
        <v-col v-else v-for="product in basketItems" :key="product.id" cols="12" lg="12" sm="12">
          <ProductBasket
            v-if="product.prod_desc"
            @selectProduct="loadSelectedProduct($event)"
            @deleteTrollyItem="updateDeleteItemDialog($event)"
            v-bind:product="product"
            v-bind:visible="true"
          ></ProductBasket>
        </v-col>
      </v-row>
    </v-col>
    <v-col cols="12" md="4" sm="12" class="hidden-md-and-down">
      <v-row justify="center" align="center">
        <v-col cols="12" lg="12" sm="12">
          <v-btn
            color="blue darken-2"
            class="white--text"
            @click="preCheckoutChecks()"
            block
            large
            :disabled="disableSubmit"
            :loading="submitLoading"
          >
            <span>Proceed to Checkout</span>
          </v-btn>
        </v-col>
        <v-col cols="12" lg="12" sm="12">
          <v-btn color="blue darken-2" exact :to="{name: OrderHereLink}" block outlined large>
            <span>{{OrdersSearchText.Full}}</span>
          </v-btn>
        </v-col>
        <v-col cols="12" lg="12" sm="12">
          <v-btn
            color="blue darken-2"
            exact
            :to="{name:'OrdersCreate'}"
            :disabled="disableCancel"
            block
            outlined
            large
          >
            <span>Change Delivery Details</span>
          </v-btn>
        </v-col>
        <v-col cols="12" lg="12" sm="12">
          <v-btn
            color="error"
            :loading="loadingCancelOrder"
            @click="cancelOrderDialog = true"
            block
            outlined
            large
            :disabled="disableCancel"
          >
            <span>Cancel Order</span>
          </v-btn>
        </v-col>
      </v-row>
    </v-col>

    <v-dialog v-model="editBasketDialog" persistent max-width="590">
      <v-card>
        <v-toolbar dense flat color="background">
          <v-toolbar-title>Delete Item</v-toolbar-title>
          <v-spacer />
          <v-btn icon @click="editBasketDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <div class="px-4">
          <ProductName :product="selectedProduct"></ProductName>
        </div>
        <v-card-text>
          <PriceSelect
            @refreshBasket="refreshBasket()"
            :product="selectedProduct"
            :isBasketItem="true"
          ></PriceSelect>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog v-model="deleteItemDialog" persistent max-width="400">
      <v-card>
        <v-toolbar color="background" flat dense>
          <v-toolbar-title>Delete Item</v-toolbar-title>
          <v-spacer />
          <v-btn icon @click="deleteItemDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <div class="pa-4">
          <ProductName :product="selectedProduct"></ProductName>
        </div>
        <v-card-subtitle>Are you sure you want to delete this item from your basket?</v-card-subtitle>
        <v-card-actions>
          <v-btn text @click="deleteItemDialog = false">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn outlined color="error" @click="delTrollyItem()">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="cancelOrderDialog" persistent max-width="400">
      <v-card>
        <v-toolbar color="background" flat dense>
          <v-toolbar-title>Cancel Order</v-toolbar-title>
          <v-spacer />
          <v-btn icon @click="cancelOrderDialog = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-subtitle>Are you sure you want to cancel your order?</v-card-subtitle>
        <v-card-subtitle>This will remove all items from your basket</v-card-subtitle>
        <v-card-actions>
          <v-btn text @click="cancelOrderDialog = false">Cancel</v-btn>
          <v-spacer></v-spacer>
          <v-btn outlined color="error" @click="delTrolly()">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-bottom-navigation inset fixed grow color="primary" class="hidden-lg-and-up">
      <v-btn
        :loading="loadingCancelOrder"
        @click="cancelOrderDialog = true"
        :disabled="disableCancel"
      >
        <span>Cancel</span>
        <v-icon>delete</v-icon>
      </v-btn>

      <v-btn exact :to="{name: OrderHereLink}">
        <span>{{OrdersSearchText.Bottom}}</span>
        <v-icon>add</v-icon>
      </v-btn>

      <v-btn exact :to="{name:'OrdersCreate'}" :disabled="disableCancel">
        <span>Edit Details</span>
        <v-icon>edit</v-icon>
      </v-btn>

      <v-btn @click="preCheckoutChecks()" :loading="submitLoading" :disabled="disableSubmit">
        <span>Checkout</span>
        <v-icon>check_circle_outline</v-icon>
      </v-btn>
    </v-bottom-navigation>
  </v-row>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";
import CncSwitch from "@/components/order/CncSwitch.vue";
import CncAddress from "@/components/order/CncAddress.vue";
import MandatoryCategory from "@/components/order/MandatoryCategory.vue";
import InvalidDate from "@/components/order/InvalidDate.vue";

import MinimumValue from "@/components/order/MinimumValue.vue";
import PriceSelect from "@/components/product/PriceSelect.vue";
import ProductBasket from "@/components/order/search/ProductBasket.vue";
import ProductName from "@/components/product/ProductName.vue";
import CutOff from "@/components/order/Cutoff.vue";


export default {
  name: "Basket",

  components: {
    CncSwitch,
    CncAddress,
    MandatoryCategory,
    InvalidDate,
    MinimumValue,
    PriceSelect,
    ProductBasket,
    ProductName,
    CutOff
  },

  data() {
    return {
      editBasketDialog: false,
      deleteItemDialog: false,
      selectedProduct: null,

      cancelOrderDialog: false,
      loadingCancelOrder: false,
      loadingDelete: false,
      submitLoading: false
    };
  },

  methods: {
    ...mapActions(["setSnackbar", "getTrollys", "orderBeingPaidForAction"]),

    loadSelectedProduct(newProduct) {
      this.selectedProduct = newProduct;
      this.editBasketDialog = true;
    },

    updateDeleteItemDialog(newProduct) {
      this.selectedProduct = newProduct;
      this.deleteItemDialog = true;
    },

    async preCheckoutChecks() {
      this.submitLoading = true;
      let checksError = false;
      let checksMessage = "";

      let payload = JSON.stringify({
        sessionKey: this.$store.getters.retrieveUser.sessionKey,
        userId: this.$store.getters.retrieveUser.userNum,
        trollyId: this.$store.getters.retrieveCurrentTrolly.id
      });

      const params = new URLSearchParams();
      params.append("params", payload);

      await axios
        .post("get-stock", params)
        .then(response => {
          if (response.data.success) {
            //this.setSnackbar({ text: "Item deleted from basket" });
          } else {
            // this.setSnackbar({
            //   color: "error",
            //   text: "Error adding item to basket"
            // });
            checksError = true;
            checksMessage += response.data.message;
          }
        })
        .catch(err => {
          console.log(err);
        });

      if (checksError == false) {
        this.$router.push({ name: "OrdersCheckout" });
      } else {
        console.log(checksMessage);
        this.setSnackbar({
          color: "error",
          text: checksMessage
        });
      }

      this.submitLoading = false;
    },

    async refreshBasket() {
      console.log("-- Close Popups");
      this.editBasketDialog = false;
      this.deleteItemDialog = false;

      console.log("-- Refresh Basket");
      await this.getTrollys()
        .then(response => {
          console.log("-- Got Trollys");
          console.log(response);
        })
        .catch(er => {
          console.log(er);
        });
    },

    async delTrollyItem() {
      console.log("Delete Item");
      this.loadingDelete = true;

      let stringPayload = JSON.stringify({
        sessionKey: this.$store.getters.retrieveUser.sessionKey,
        userId: this.$store.getters.retrieveUser.userNum,
        trollyId: this.$store.getters.retrieveCurrentTrolly.id,
        prodNo: this.selectedProduct.prod_no,
        qtyType: this.selectedProduct.qty_type
      });

      const params = new URLSearchParams();
      params.append("params", stringPayload);

      await axios
        .post("del-trolly-item", params)
        .then(response => {
          if (response.data.success) {
            this.setSnackbar({ text: "Item deleted from basket" });
            this.refreshBasket();
          } else {
            this.setSnackbar({
              color: "error",
              text: "Error adding item to basket"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });

      this.loadingDelete = false;
      this.editBasketDialog = false;
    },

    async delTrolly() {
      console.log("Delete Trolly");
      this.loadingCancelOrder = true;

      let stringPayload = JSON.stringify({
        sessionKey: this.$store.getters.retrieveUser.sessionKey,
        userId: this.$store.getters.retrieveUser.userNum,
        trollyId: this.$store.getters.retrieveCurrentTrolly.id
      });

      const params = new URLSearchParams();
      params.append("params", stringPayload);

      await axios
        .post("del-trolly", params)
        .then(response => {
          if (response.data.success) {
            this.setSnackbar({ text: "Order Cancelled" });
            this.refreshBasket();
          } else {
            this.setSnackbar({
              color: "error",
              text: "Error cancelling order"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });

      this.cancelOrderDialog = false;
      this.loadingCancelOrder = false;
    }
  },

  computed: {
    OrdersSearchText() {
      let searchText = {};

      if (this.$store.getters.retrieveCurrentTrolly) {
        if (
          Object.keys(this.$store.getters.retrieveCurrentTrolly.trolly_items)
            .length > 0
        ) {
          searchText.Full = "Continue Shopping";
          searchText.Bottom = "Add Items";
        } else {
          searchText.Full = "Start Shopping";
          searchText.Bottom = "Search";
        }
      } else {
        searchText.Full = "Start Shopping";
        searchText.Bottom = "Search";
      }

      return searchText;
    },

    disableSubmit() {
      return !this.$store.getters.retrieveCurrentTrolly.valid;
    },

    disableCancel() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return false;
      } else {
        return true;
      }
    },

    totalBasketItems() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        if (
          Object.keys(this.$store.getters.retrieveCurrentTrolly.trolly_items)
            .length > 0
        ) {
          return Object.keys(
            this.$store.getters.retrieveCurrentTrolly.trolly_items
          ).length;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },

    totalQty() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return this.$store.getters.retrieveCurrentTrolly.totalQty;
      } else {
        return 0;
      }
    },

    basketItems() {
      if (this.totalBasketItems) {
        return this.$store.getters.retrieveCurrentTrolly.trolly_items;
      } else {
        return false;
      }
    },

    basketTotal() {
      if (this.totalBasketItems) {
        return this.$store.getters.retrieveCurrentTrolly.totalValue;
      } else {
        return false;
      }
    },

    OrderHereLink() {
      console.log("-- Checking Order Here Link");
      console.log(this.$store.getters.retrieveCurrentTrolly);

      if (this.$store.getters.retrieveCurrentTrolly) {
        return "OrdersSearch";
      } else {
        return "OrdersCreate";
      }
    }
  },

  activated() {
    this.refreshBasket();
  }
};
</script>

