<template>
  <div>
    <v-row justify="center" align="center">
      <v-col cols="12" md="11" sm="12">
        <v-banner icon="list_alt" single-line>Your Orders</v-banner>
        <v-btn
          block
          outlined
          color="blue darken-2"
          x-large
          @click.stop="fetchExistingOrders"
          class="mb-4"
        >
          <v-icon class="mr-2">refresh</v-icon>Refresh
        </v-btn>
        <v-data-table :headers="headers" :items="orders" hide-default-footer disable-pagination class="elevation-1">
          <template v-slot:item.deliveryDate="{item}">
            <span>
              <span class="font-weight-medium">{{getFormatedDate(item.transaction_date)}}</span>
              <br />
            </span>
          </template>
          <template v-slot:item.total="{item}">
            <span>
              <span class="font-weight-medium">£ {{parseFloat(item.net_total).toFixed(2)}}</span>
              <br />
            </span>
          </template>
          <template v-slot:item.ticketStatus="{item}">
            <span>
              <span class="font-weight-medium">{{getOrderStatus(item,item.tickets)}}</span>
              <br />
            </span>
          </template>
          <template v-slot:item.actions="{item}">
            <v-tooltip
              top
              v-if="item.locked == '1' && item.payment_found == false && item.payment_success == null || item.locked == '1' && item.payment_success == '0'"
            >
              <template v-slot:activator="{ on }">
                <v-btn icon color="blue darken-2" v-on="on" @click.stop="goToPaymentPage(item)">
                  <v-icon>payment</v-icon>
                </v-btn>
              </template>
              <span>Make Payment</span>
            </v-tooltip>
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <v-btn icon color="blue darken-2" v-on="on" @click.stop="resendEmail(item.td_id)">
                  <v-icon>mail</v-icon>
                </v-btn>
              </template>
              <span>Resend Email</span>
            </v-tooltip>
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <v-btn icon color="blue darken-2" v-on="on" @click.stop="goToDetailsPage(item)">
                  <v-icon>info</v-icon>
                </v-btn>
              </template>
              <span>More Information</span>
            </v-tooltip>
            <v-tooltip
              top
              v-if="item.locked == '1' && item.payment_found == false && item.payment_success == null || item.locked == '1' && item.payment_success == '0'"
            >
              <template v-slot:activator="{ on }">
                <v-btn icon color="error darken-1" v-on="on" @click="showVoidDialog(item.td_id)">
                  <v-icon>delete_forever</v-icon>
                </v-btn>
              </template>
              <span>Void Order</span>
            </v-tooltip>
          </template>
        </v-data-table>
      </v-col>
    </v-row>

    <v-dialog v-model="voidOrderDialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">Void Order</v-card-title>
        <v-card-text>This will permanently remove your order, you will not be able to retrieve this order once voided, Are you sure?</v-card-text>
        <v-card-actions>
          <v-btn text @click="hideVoidDialog()">back</v-btn>
          <v-spacer></v-spacer>
          <v-btn :loading="voidLoading" color="error darken-1" outlined @click="voidItem()">Void</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import moment from "moment";
export default {
  name: "OrdersYourOrders",
  data: () => {
    return {
      headers: [
        // { text: "Transaction ID", value: "id" },
        { text: "Delivery Date", value: "deliveryDate" },
        { text: "Order Total", value: "total", sortable: false },
        { text: "Status", value: "ticketStatus" },
        { text: "Actions", value: "actions", sortable: false }
      ],
      voidOrderDialog: false,
      tdidToVoid: null,
      voidLoading: false
    };
  },
  computed: {
    ...mapGetters(["retrieveUnpaidOrders"]),
    orders() {
      return this.$store.state.ordersHistory.orders;
    }
  },

  methods: {
    ...mapActions([
      "fetchExistingOrders",
      "sendEmail",
      "voidOrder",
      "clearOrderProducts",
      "clearOrderTickets",
      "orderBeingPaidForAction",
      "setSnackbar"
    ]),
    getInvoiceNo(ticket) {
      if (ticket) {
        return Object.keys(ticket)[0];
      } else {
        return "";
      }
    },
    hasMultipleInvoices(order) {
      return Object.keys(order.tickets).length > 1;
    },
    getOrderStatus(order, ticket) {
      let invoice = this.getInvoiceNo(ticket);
      if (order) {
        let statusCode = order.tickets[invoice].status;
        if (
          order.locked == 1 &&
          order.payment_found == true &&
          order.payment_success == "0"
        ) {
          return "Payment Failed";
        } else if (
          order.locked == 1 &&
          order.payment_found == false &&
          order.payment_success == null
        ) {
          return "Awaiting Payment";
        } else if (
          order.locked == 1 &&
          order.payment_found == true &&
          order.payment_success == null
        ) {
          return "Processing Payment";
        }
        if (statusCode == 1) {
          return "Order Entered";
        } else if (statusCode == 2) {
          return "Order Picked";
        } else if (statusCode == 3) {
          return "Order Dispatched";
        } else if (statusCode == 4) {
          return "Order Invoiced";
        } else {
          return "Order";
        }
      } else {
        return "";
      }
    },
    resendEmail(tdId) {
      this.sendEmail(tdId)
        .then(response => {
          if (response.success == true) {
            this.setSnackbar({ text: "Email Sent Successfully" });
            console.log("Sent Email successfully");
          } else {
            this.setSnackbar({ color: "error", text: response.message });
            console.log("Sending failed");
          }
        })
        .catch(er => {
          console.log(er);
        });
    },

    showVoidDialog(tdId) {
      console.log("-- TD ID to void");
      this.tdidToVoid = tdId;
      console.log(this.tdidToVoid);
      this.voidOrderDialog = true;
    },

    hideVoidDialog() {
      console.log("-- TD ID to void");
      this.tdidToVoid = null;
      this.voidOrderDialog = false;
    },

    voidItem() {
      console.log("Void ORder");
      console.log(this.tdidToVoid);

      this.voidLoading = true;

      this.voidOrder(this.tdidToVoid)
        .then(response => {
          if (response.success == true) {
            this.setSnackbar({ text: "Order Voided" });
            this.fetchExistingOrders();
          } else {
            this.setSnackbar({ color: "error", text: response.message });
          }

          this.voidOrderDialog = false;
          this.voidLoading = false;
        })
        .catch(er => {
          console.log(er);
          this.voidOrderDialog = false;
          this.voidLoading = false;
          this.setSnackbar({
            color: "error",
            text: "Order Not Voided: Connection to server lost"
          });
        });
    },

    getFormatedDate(deliveryDate) {
      return moment(deliveryDate).format("dddd, MMMM Do YYYY");
    },
    getOrderProducts(ord) {
      this.$store
        .dispatch("fetchOrdersProducts", { tdId: ord.td_id })
        .then(response => {
          if (response == true) {
            console.log("Products Found And Retrieved");
          } else {
            console.log("Failed to retreive Order Products");
          }
        })
        .catch(er => {
          console.log(er);
        });
    },
    goToDetailsPage(order) {
      if (this.hasMultipleInvoices(order)) {
        this.$store.dispatch("fetchOrderTickets", { tickets: order.tickets });
        this.$router.push({
          name: "OrderDetails",
          params: { td_id: order.td_id }
        });
        console.log(order.td_id);
      } else {
        this.getOrderProducts(order);
        this.$router.push({
          name: "TicketDetails",
          params: {
            td_id: order.td_id,
            invoice_no: this.getInvoiceNo(order.tickets)
          }
        });
      }
    },
    goToPaymentPage(order) {
      //set store for orderBeingPaidFor
      const rec = {
        orderId: order.td_id,
        orderAmount: order.net_total
      };
      this.orderBeingPaidForAction(rec);
      this.$router.push({ name: "OrderPayment" });
      //redirect
    }
  },
  activated() {
    this.fetchExistingOrders()
      .then(response => {
        if (response == true) {
          console.log("fetched orders");
          this.clearOrderProducts();
          this.clearOrderTickets();
        } else {
          console.log("error fetchig orders");
        }
      })
      .catch(er => {
        console.log(er);
      });
  }
};
</script>

<style scoped>
tbody tr:nth-of-type(even) {
  background-color: rgba(236, 237, 237);
}

tbody tr:nth-of-type(odd) {
  background-color: rgb(250, 250, 250);
}
</style>