<template>
  <v-alert v-if="!validDate" prominent type="error" class="ma-0 px-4 py-2" outlined text>
    <v-row align="center">
      <v-col class="grow">Your order date of {{computedDateFormattedMomentjs}} is invalid</v-col>
      <v-col class="shrink">
        <v-btn color="error" :to="{name:'OrdersCreate'}">Amend</v-btn>
      </v-col>
    </v-row>
  </v-alert>
</template>

<script>
import moment from "moment";

export default {
  computed: {
    computedDateFormattedMomentjs() {
      return this.$store.getters.retrieveCurrentTrolly.order_date
        ? moment(this.$store.getters.retrieveCurrentTrolly.order_date).format(
            "dddd, Do MMMM YYYY"
          )
        : "";
    },

    validDate() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return this.$store.getters.retrieveCurrentTrolly.delivery_date_valid
          .valid;
      }
      return true;
    },

    dateInfo() {
      return this.$store.getters.retrieveCurrentTrolly;
    }
  }
};
</script>

