<script>
export default {
  props: {
    product: {}
  },
  data() {
    return {
      tags: null
    };
  },
  computed: {
    boxPrice() {
      return parseFloat(this.product.box_price).toFixed(2);
    },
    kiloPrice() {
      return parseFloat(this.product.kilo_price).toFixed(2);
    },
    eachPrice() {
      return parseFloat(this.product.each_price).toFixed(2);
    },

    boxPriceDisplay() {
      return parseFloat(this.product.box_price).toFixed(2);
    },

    kiloPriceDisplay() {
      return parseFloat(this.product.kilo_price).toFixed(2);
    },

    eachPriceDisplay() {
      if (
        !this.$store.getters.retrieveIsCncCustomer &&
        !this.$store.getters.retrieveIsCashCustomer &&
        parseInt(this.product.catch_weight_flag) == 2
      ) {
        return parseFloat(this.product.kilo_price).toFixed(2) + "/kg";
      } else {
        return parseFloat(this.product.each_price).toFixed(2);
      }
    }
  }
};
</script>

