<template>
  <div>
    <CutOff/>
    <v-dialog v-model="showGroups" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-toolbar flat color="background">
        <v-toolbar-title>Groups</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="showGroups = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-card flat color="background">
        <v-row justify="center" align="start" class="ma-0" style="width:100%">
          <v-col cols="12" md="10" xs="12">
            <v-row justify="start" align="start">
              <v-col
                cols="12"
                xl="2"
                lg="3"
