              >
                <v-card class="mx-auto" outlined max-width="200px" height="100%">
                  <GroupImage :group="group"></GroupImage>
                  <v-card-title>{{group.group_description}}</v-card-title>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn dark text color="blue darken-2" @click="getGroup(group.group_no)">Select</v-btn>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card>
    </v-dialog>
    <v-row justify="center" align="start">
      <v-col cols="12" md="12" xs="12" class="py-0">
        <MandatoryCategory @getMandatoryCat="getMandatoryCat()"></MandatoryCategory>
      </v-col>
      <v-col cols="12" md="12" xs="12">
        <v-row justify="start" align="start" no-gutters>
          <v-col cols="12" md="12" xs="12" class="hidden-md-and-down">
            <v-row justify="space-between" align="start">
              <v-col cols="12" sm="3" xs="12" class="pt-0">
                <v-btn
                  :loading="loadingGroup"
                  block
                  outlined
                  color="blue darken-2"
                  @click="showGroups = true"
                >
                  <v-icon left>mdi-ungroup</v-icon>View by Group
                </v-btn>
              </v-col>
              <v-col cols="12" sm="3" xs="12" class="pt-0">
                <v-btn
                  :loading="loadingFavourites"
                  block
                  outlined
                  color="favourites"
