                >
                  <v-icon left>mdi-heart</v-icon>
                  {{favouritesText}}
                </v-btn>
              </v-col>
              <v-col cols="12" sm="3" xs="12" class="pt-0">
                <v-btn
                  :loading="loadingPromotions"
                  block
                  outlined
                  color="promotions"
                  @click="getPromotions()"
                >
                  <v-icon left>mdi-sale</v-icon>Promotions
                </v-btn>
              </v-col>
            </v-row>
          </v-col>

          <v-col cols="12" md="12" xs="12">
            <v-row justify="space-between" align="start">
              <v-col cols="12" sm="5" xs="12">
                <v-form ref="searchForm" @submit.prevent="productSearch">
                  <v-text-field
                    :loading="loadingSearch"
                    v-model="search"
                    ref="search"
                    :rules="searchRules"
                    placeholder="Product Search"
                    filled
                    clearable
                    @input="debounceSearch"
                  >
                    <template slot="append">
                      <v-btn
                        icon
                        class="mr-0 ml-2 mb-1 p-0"
                        style="margin-top: -6px"
                        @click="productSearch()"
                      >
                        <v-icon>mdi-magnify</v-icon>
                      </v-btn>
                    </template>
                  </v-text-field>
                </v-form>
              </v-col>

              <v-col cols="12" md="7" xs="12" class="px-0">
                <v-btn-toggle
                  v-model="productFilters"
                  multiple
                  tile
                  class="pr-2"
                  style="width:100%"
                >
                  <v-layout row wrap justify-end style="text-alight: right">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn
                          v-on="on"
                          value="vegetarian"
                          class="pa-0 mr-1"
                          @click="page = 1"
                          active-class="test"
                        >
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/vegetarian.png')"
                            :gradient="!productFilters.includes('vegetarian') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Vegetarian</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="vegan" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/vegan.png')"
                            :gradient="!productFilters.includes('vegan') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Vegan</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="glutenFree" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/glutenFree.jpg')"
                            :gradient="!productFilters.includes('glutenFree') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Gluten Free</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="coeliac" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/coeliac.jpg')"
                            :gradient="!productFilters.includes('coeliac') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Coeliac</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="fairTrade" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/fairTrade.png')"
                            :gradient="!productFilters.includes('fairTrade') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Fair Trade</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="uk" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/uk.png')"
                            :gradient="!productFilters.includes('uk') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>UK</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="local" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/local.jpg')"
                            :gradient="!productFilters.includes('local') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Local</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="redTractor" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/redTractor.png')"
                            :gradient="!productFilters.includes('redTractor') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Red Tractor</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="halal" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/halal.png')"
                            :gradient="!productFilters.includes('halal') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Halal</span>
                    </v-tooltip>

                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" value="kosher" class="pa-0 mr-1">
                          <v-img
                            contain
                            width="46px"
                            height="46px"
                            :src="require('@/assets/filters/kosher.png')"
                            :gradient="!productFilters.includes('kosher') ? '' : 'to top right, rgba(0,0,0,.3), rgba(0,0,0,.3)'"
                          />
                        </v-btn>
                      </template>
                      <span>Kosher</span>
                    </v-tooltip>
                  </v-layout>
                </v-btn-toggle>
              </v-col>

              <v-col cols="12" sm="12" xs="12" class="pa-0">
                <v-chip
                  class="ma-2"
                  filter
                  v-for="(product, index) in productFilters"
                  :key="index"
                  style="text-transform: capitalize;"
                >{{product}}</v-chip>
              </v-col>
            </v-row>

            <v-layout v-if="isProdGrid" row wrap justify-start>
              <ProductGrid
                v-for="product in filteredPage"
                :key="product.prod_no"
                v-bind:product="product"
                v-bind:visible="true"
              ></ProductGrid>
            </v-layout>
            <v-layout v-else row wrap justify-start>
              <ProductList
                v-for="product in filteredPage"
                :key="product.prod_no"
                v-bind:product="product"
                v-bind:visible="true"
              ></ProductList>
            </v-layout>

            <v-pagination
              v-if="filteredProds.length > 0"
              v-model="page"
              :length="pages"
              prev-icon="mdi-menu-left"
              next-icon="mdi-menu-right"
              color="blue darken-2"
            ></v-pagination>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-bottom-navigation inset fixed grow color="primary" class="hidden-lg-and-up">
      <v-btn @click="focusSearch()">
        <span>Search</span>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>

      <v-btn @click="showGroups = true">
        <span>View Groups</span>
        <v-icon>mdi-ungroup</v-icon>
      </v-btn>

      <v-btn @click="getFavourites()">
        <span>Favourites</span>
        <v-icon>mdi-heart</v-icon>
      </v-btn>

      <v-btn @click="getPromotions()">
        <span>Promotions</span>
        <v-icon>mdi-sale</v-icon>
      </v-btn>
    </v-bottom-navigation>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import axios from "axios";

import GroupImage from "@/components/group/GroupImage.vue";
import ProductGrid from "@/components/order/search/ProductGrid.vue";
import ProductList from "@/components/order/search/ProductList.vue";
import MandatoryCategory from "@/components/order/MandatoryCategory.vue";
import CutOff from "@/components/order/Cutoff.vue";


export default {
  name: "ProductSearch",

  props: ["category"],

  components: {
    GroupImage,
    ProductGrid,
    ProductList,
    MandatoryCategory,
    CutOff
  },
  data() {
    return {
      showGroups: false,
      productGrid: this.$store.getters.retrieveProductGrid,
      loadingSearch: false,
      loadingGroup: false,
      loadingFavourites: false,
      loadingPromotions: false,
      prods: [],
      page: 1,
      groups: [],
      productFilters: [],
      search: "",
      searchRules: [
        //v => !!v || "Search can't be ",
        v =>
          (v && v.length >= 3 && v.length > 0) ||
          "Search must be at least 3 characters"
      ]
    };
  },

  async beforeMount() {
    console.log("-- Before Mount");
    console.log(" -- Checking Order Date");

    console.log(this.$store.getters.retrieveCurrentTrolly);

    //Check if we have a valid troll before we let the user access this route
    if (this.$store.getters.retrieveCurrentTrolly == false) {
      this.$router.push({ name: "OrdersCreate" });
      return;
    }

    let stringPayload = JSON.stringify({
      userId: this.$store.getters.retrieveUser.userNum,
      sessionKey: this.$store.getters.retrieveUser.sessionKey,
      orderDate: this.$store.getters.retrieveCurrentTrolly.order_date
    });

    const params = new URLSearchParams();
    params.append("params", stringPayload);

    await axios
      .post("get-prod-group-cat", params)
      .then(response => {
        console.log("-- Group Cat Response");
        console.log(response);
        this.groups = response.data.prodgroupscats;
        this.setGroups(this.groups);
      })
      .catch(err => {
        console.log(err);
      });
  },

  mounted() {
    this.checkAutoLoad();
  },

  computed: {
    favouritesText() {
      if (
        this.$store.getters.retrieveIsCncCustomer ||
        this.$store.getters.retrieveIsCashCustomer
      ) {
        return "Our Favourites";
      }

      return "Favourites";
    },
    isProdGrid() {
      if (this.$store.getters.retrieveProductGrid) {
        return true;
      } else {
        return false;
      }
    },
    filteredProds() {
      return this.prods.filter(this.filterProducts);
    },
    filteredPage() {
      let page = this.page;
      let pageLength = this.pageLength;

      let originalFilter = this.filteredProds.slice(
        (page - 1) * pageLength,
        page * pageLength
      );

      return originalFilter;
    },
    pages() {
      return this.pageLength
        ? Math.ceil(this.filteredProds.length / this.pageLength)
        : 0;
    },
    pageLength() {
      if (this.$store.getters.retrieveAllConfigs.orders_products_per_page) {
        return parseInt(
          this.$store.getters.retrieveAllConfigs.orders_products_per_page
        );
      }
      return 15;
    }
  },
  methods: {
    ...mapActions(["setGroups"]),

    checkAutoLoad() {
      console.log("-- Checking Autoload");

      switch (
        parseInt(
          this.$store.getters.retrieveAllConfigs.orders_autoload_favourites
        )
      ) {
        case 1:
          console.log("-- Autoload Favourites");
          this.getFavourites();
          break;

        case 2:
          console.log("-- Autoload Groups");
          this.showGroups = true;
          break;
      }
    },

    filterProducts(product) {
      let productFilters = this.productFilters;

      //Default our product to show
      var showProduct = true;

      if (productFilters.includes("vegetarian")) {
        if (product.vegetarian != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("vegan")) {
        if (product.vegan != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("glutenFree")) {
        if (product.glutenFree != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("coeliac")) {
        if (product.coeliac != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("fairTrade")) {
        if (product.fairTrade != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("uk")) {
        if (product.uk != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("local")) {
        if (product.local != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("redTractor")) {
        if (product.redTractor != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("kosher")) {
        if (product.kosher != true) {
          showProduct = false;
        }
      }

      if (productFilters.includes("halal")) {
        if (product.halal != true) {
          showProduct = false;
        }
      }

      return showProduct;
    },

    debounceSearch() {
      clearTimeout(this.debounce);
      this.debounce = setTimeout(() => {
        console.log(this.search);
        this.productSearch();
      }, 600);
    },

    async productSearch() {
      if (this.$refs.searchForm.validate()) {
        this.page = 1;
        this.loadingSearch = true;

        let stringPayload = JSON.stringify({
          userId: this.$store.getters.retrieveUser.userNum,
          sessionKey: this.$store.getters.retrieveUser.sessionKey,
          search: this.search,
          orderDate: this.$store.getters.retrieveCurrentTrolly.order_date
        });
        const params = new URLSearchParams();
        params.append("params", stringPayload);

        await axios
          .post("product-search", params)
          .then(response => {
            this.loadingSearch = false;

            if (response.data.success) {
              this.prods = response.data.prods;
            }
          })
          .catch(err => {
            console.log(err);
          });

        this.loadingSearch = false;
      }
    },

    async getFavourites() {
      this.page = 1;
      this.loadingSearch = true;
      this.loadingFavourites = true;

      let stringPayload = JSON.stringify({
        userId: this.$store.getters.retrieveUser.userNum,
        sessionKey: this.$store.getters.retrieveUser.sessionKey,
        orderDate: this.$store.getters.retrieveCurrentTrolly.order_date
      });
      const params = new URLSearchParams();
      params.append("params", stringPayload);

      await axios
        .post("get-favourites", params)
        .then(response => {
          if (response.data.success) {
            this.prods = response.data.prods;
          }
        })
        .catch(err => {
          console.log(err);
        });

      this.loadingSearch = false;
      this.loadingFavourites = false;
    },

    async getPromotions() {
      this.page = 1;
      this.loadingSearch = true;
      this.loadingPromotions = true;

      let stringPayload = JSON.stringify({
        userId: this.$store.getters.retrieveUser.userNum,
        sessionKey: this.$store.getters.retrieveUser.sessionKey,
        orderDate: this.$store.getters.retrieveCurrentTrolly.order_date
      });
      const params = new URLSearchParams();
      params.append("params", stringPayload);

      await axios
        .post("get-promotions", params)
        .then(response => {
          if (response.data.success) {
            this.prods = response.data.prods;
          }
        })
        .catch(err => {
          console.log(err);
        });

      this.loadingSearch = false;
      this.loadingPromotions = false;
    },

    async getGroup(groupNum) {
      this.page = 1;
      this.showGroups = false;
      this.loadingSearch = true;
      this.loadingGroup = true;

      let stringPayload = JSON.stringify({
        userId: this.$store.getters.retrieveUser.userNum,
        sessionKey: this.$store.getters.retrieveUser.sessionKey,
        orderDate: this.$store.getters.retrieveCurrentTrolly.order_date,
        groupNum: groupNum
      });
      const params = new URLSearchParams();
      params.append("params", stringPayload);

      await axios
        .post("get-group-price-list", params)
        .then(response => {
          if (response.data.success) {
            this.prods = response.data.prods;
          }
        })
        .catch(err => {
          console.log(err);
        });

      this.loadingSearch = false;
      this.loadingGroup = false;
    },

    async getMandatoryCat() {
      this.page = 1;
      this.showGroups = false;
      this.loadingSearch = true;
      this.loadingGroup = true;

      let stringPayload = JSON.stringify({
        userId: this.$store.getters.retrieveUser.userNum,
        sessionKey: this.$store.getters.retrieveUser.sessionKey,
        orderDate: this.$store.getters.retrieveCurrentTrolly.order_date,
        catNum: this.$store.getters.retrieveCurrentTrolly.mandatory_cat
          .category_no
      });
      const params = new URLSearchParams();
      params.append("params", stringPayload);

      await axios
        .post("get-cat-price-list", params)
        .then(response => {
          if (response.data.success) {
            this.prods = response.data.prods;
          }
        })
        .catch(err => {
          console.log(err);
        });

      this.loadingSearch = false;
      this.loadingGroup = false;
    },

    focusSearch() {
      console.log(this.$refs.search);
      this.$refs.search.focus();

      console.log(this.$refs.search);
      //this.$refs.search.$el.select();
    }
  }
};
</script>

<style lang="sass" scoped>
.test
  background: linear-gradient(to right top, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3))

.v-card__title
  font-size: 16px
  font-weight: bold

.v-card__subtitle
  padding: 4px 1em
</style>