<template>
  <v-row justify="start" align="start">
    <v-col cols="12" md="8" sm="12">
      <v-row justify="start" align="start">
        <v-col cols="12" md="4" sm="12">
          <div
            class="title"
            v-if="showSafePlace || showAdditonalInstructions || showDisclaimerCheckbox"
          >Delivery Information</div>
        </v-col>
        <v-col cols="12" md="8" sm="12">
          <v-form class="pa-4" ref="checkoutForm" @submit.prevent>
            <v-select
              v-if="showSafePlace"
              v-model="safePlace"
              :items="safePlaces"
              :rules="safePlaceRules"
              prepend-icon="lock"
              label="Safe Place"
              dense
            ></v-select>
            <v-textarea
              v-if="showAdditonalInstructions"
              v-model="additionalInstructions"
              class="pt-4"
              clearable
              label="Additional Instructions"
              type="text"
              filled
              auto-grow
              rows="3"
              placeholder="If you are self isolating please specify"
              counter
              maxlength="200"
            ></v-textarea>
            <v-checkbox
              v-if="showDisclaimerCheckbox"
              v-model="disclaimerCheckbox"
              :rules="disclaimerCheckboxRules"
              label="I accept the disclaimer"
              color="blue darken-2"
            >
              <template v-slot:label>
                I accept the
                <a
                  class="ml-1"
                  style="color: blue darken-2"
                  href="#"
                  @click.stop.prevent="disclaimerDialog = true"
                >&nbsp;Disclaimer</a>
              </template>
            </v-checkbox>
          </v-form>
        </v-col>
        <v-col cols="12" md="4" sm="12">
          <div class="title" v-if="showCncAddress">Collection Order</div>
          <div class="title" v-else>Deliver To</div>
        </v-col>
        <v-col cols="12" md="8" sm="12">
          <CncAddress v-if="showCncAddress" class="mb-4"></CncAddress>
          <CustomerAddress v-else></CustomerAddress>
        </v-col>

        <v-col cols="12" md="12" sm="12">
          <v-btn
            :loading="purchaseOrderLoading"
            color="blue darken-2"
            class="white--text"
            @click="purchaseOrder()"
          >Buy Now</v-btn>
        </v-col>

        <v-dialog v-model="disclaimerDialog" absolute max-width="400" persistent>
          <v-card>
            <v-toolbar color="background" flat dense>
              <v-toolbar-title>Disclaimer</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn icon @click="disclaimerDialog = false">
                <v-icon>mdi-close</v-icon>
              </v-btn>
            </v-toolbar>
            <v-card-text>{{disclaimerContent}}</v-card-text>
            <v-card-actions>
              <v-btn text @click="disclaimerCheckbox = false, disclaimerDialog = false">Cancel</v-btn>
              <v-spacer></v-spacer>
              <v-btn
                text
                color="blue darken-2"
                @click="disclaimerCheckbox = true, disclaimerDialog = false"
              >Accept</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
    </v-col>
    <v-col cols="12" md="4" sm="12">
      <v-btn
        :loading="purchaseOrderLoading"
        block
        color="blue darken-2"
        class="white--text"
        @click="purchaseOrder()"
      >Buy Now</v-btn>

      <v-card class="mt-4" outlined>
        <v-card-title>Order Summary</v-card-title>
        <v-card-text>
          No. Items:
          <b>{{totalBasketItems}}</b>
        </v-card-text>
        <v-card-text>
          Total Quantity:
          <b>{{totalQty}}</b>
        </v-card-text>
        <v-card-text>
          Total Value:
          <b>£{{basketTotal}}</b>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import CncAddress from "@/components/order/CncAddress.vue";
import CustomerAddress from "@/components/customer/CustomerAddress.vue";

import axios from "axios";
import { mapActions, mapGetters } from "vuex";

export default {
  components: {
    CncAddress,
    CustomerAddress
  },
  data() {
    return {
      additionalInstructions: "",

      safePlace: null,
      safePlaceRules: [v => !!v || "You must choose a safe place"],
      safePlaces: [
        "Front Door",
        "Shed",
        "Back door",
        "Behind Wheelie Bin",
        "With next-door-neighbour",
        "Garden",
        "None of the above"
      ],

      disclaimerDialog: false,
      disclaimerCheckbox: false,
      disclaimerCheckboxRules: [v => !!v || "You must accept the disclaimer"],

      purchaseOrderLoading: false
    };
  },

  computed: {
    ...mapGetters(["retrieveCustomerAddress"]),

    showSafePlace() {
      let showSelector = false;
      let config = this.$store.getters.retrieveAllConfigs
        .orders_delivery_safe_place;

      if (
        config == 1 ||
        (config == 2 && this.$store.getters.retrieveIsCashCustomer)
      ) {
        showSelector = true;
      }

      return showSelector;
    },

    showAdditonalInstructions() {
      let showSelector = false;
      let config = this.$store.getters.retrieveAllConfigs
        .orders_delivery_instructions_show;

      if (
        config == 1 ||
        (config == 2 && this.$store.getters.retrieveIsCashCustomer)
      ) {
        showSelector = true;
      }

      return showSelector;
    },

    showDisclaimerCheckbox() {
      let showSelector = false;
      let config = this.$store.getters.retrieveAllConfigs
        .orders_create_disclaimer_checkbox;

      if (
        config == 1 ||
        (config == 2 && this.$store.getters.retrieveIsCashCustomer) ||
        (config == 2 && this.$store.getters.retrieveIsCncCustomer)
      ) {
        showSelector = true;
      }

      return showSelector;
    },

    disclaimerContent() {
      let disclaimerContent = "";

      if (
        this.$store.getters.retrieveAllConfigs
          .orders_create_disclaimer_content != ""
      ) {
        disclaimerContent = this.$store.getters.retrieveAllConfigs
          .orders_create_disclaimer_content;
      }

      return disclaimerContent;
    },

    totalBasketItems() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        if (
          Object.keys(this.$store.getters.retrieveCurrentTrolly.trolly_items)
            .length > 0
        ) {
          return Object.keys(
            this.$store.getters.retrieveCurrentTrolly.trolly_items
          ).length;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },

    totalQty() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return this.$store.getters.retrieveCurrentTrolly.totalQty;
      } else {
        return 0;
      }
    },

    basketItems() {
      if (this.totalBasketItems) {
        return this.$store.getters.retrieveCurrentTrolly.trolly_items;
      } else {
        return false;
      }
    },

    basketTotal() {
      if (this.totalBasketItems) {
        return this.$store.getters.retrieveCurrentTrolly.totalValue;
      } else {
        return false;
      }
    },
    showCncAddress() {
      if (this.$store.getters.retrieveIsCncCustomer) {
        return true;
      }

      return false;
    }
  },

  methods: {
    ...mapActions(["setSnackbar", "getTrollys", "orderBeingPaidForAction"]),

    async refreshBasket() {
      console.log("-- Close Popups");
      this.editBasketDialog = false;
      this.deleteItemDialog = false;

      console.log("-- Refresh Basket");
      await this.getTrollys()
        .then(response => {
          console.log("-- Got Trollys");
          console.log(response);
        })
        .catch(er => {
          console.log(er);
        });
    },

    async purchaseOrder() {
      console.log(this.disclaimerCheckbox);
      console.log(this.safePlace);

      if (this.$refs.checkoutForm.validate()) {
        console.log("purchase-order Item");
        this.purchaseOrderLoading = true;

        let instructionsString = "";

        if (this.safePlace != null) {
          instructionsString += "Safe Place: ";
          instructionsString += this.safePlace;
        }

        if (this.additionalInstructions != "") {
          if (this.safePlace != null) {
            instructionsString += " - ";
          }
          instructionsString += this.additionalInstructions;
        }

        let stringPayload = JSON.stringify({
          sessionKey: this.$store.getters.retrieveUser.sessionKey,
          userId: this.$store.getters.retrieveUser.userNum,
          trollyId: this.$store.getters.retrieveCurrentTrolly.id,
          specialInstructions: instructionsString
        });

        const params = new URLSearchParams();
        params.append("params", stringPayload);

        await axios
          .post("purchase-order", params)
          .then(response => {
            if (response.data.success) {
              this.refreshBasket();

              if (parseInt(response.data.locked)) {
                const rec = {
                  orderId: response.data.td_id,
                  orderAmount: response.data.orderTotal
                };
                this.orderBeingPaidForAction(rec);
                this.$router.push({ name: "OrderPayment" });
              } else {
                this.$router.push({
                  name: "OrdersConfirmation",
                  params: { transaction: response.data }
                });
              }
            } else {
              console.log("-- Fail :()");
              let errorMessage = "";
              response.data.messages.forEach(function(message) {
                errorMessage += message + "\n";
              });

              this.setSnackbar({ color: "error", text: errorMessage });
            }
          })
          .catch(err => {
            console.log(err);
            this.setSnackbar({
              color: "error",
              text: "Error submitting basket"
            });
          });

        this.purchaseOrderLoading = false;
      }
    }
  }
};
</script>


<style lang="sass">
@import '~vuetify/src/styles/styles.sass'

.v-application a
  color: var(--v-info-darken2)
</style>
