<template>
  <div>
    <v-row justify="center" align="center">
      <v-col cols="12" lg="8" sm="12">
        <v-form ref="createOrderForm" @submit.prevent>
          <CncSwitch class="mb-4"></CncSwitch>
          <CncAddress class="mb-4"></CncAddress>
          <!-- <Cutoff class="mb-4" :orderDate="orderDate"></Cutoff> -->
          <OrderDate v-model="orderDate"></OrderDate>
          <OrderReference v-model="custRef"></OrderReference>
          <v-btn
            :loading="loading"
            block
            color="primary"
            x-large
            type="submit"
            @click="submit"
          >Confirm Date</v-btn>
        </v-form>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import { mapActions } from "vuex";

import CncSwitch from "@/components/order/CncSwitch.vue";
import CncAddress from "@/components/order/CncAddress.vue";

import OrderDate from "@/components/order/Date.vue";
import OrderReference from "@/components/order/Reference.vue";
import Cutoff from "@/components/order/Cutoff.vue";

export default {
  components: {
    CncSwitch,
    CncAddress,
    OrderDate,
    OrderReference,
    Cutoff
  },
  data() {
    return {
      loading: false,
      orderDate: this.getInitialDate(),
      custRef: this.getInitialRef()
    };
  },

  activated() {
    this.getCustomer()
      .then(response => {})
      .catch(er => {
        console.log(er);
      });
  },

  methods: {
    ...mapActions([
      "getCustomer",
      "createNewTrolly",
      "getTrollys",
      "updateTrollyOrderDate",
      "updateTrollyCustRef"
    ]),

    getInitialDate() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return this.$store.getters.retrieveCurrentTrolly.order_date;
      } else {
        return null;
      }
    },

    getInitialRef() {
      if (this.$store.getters.retrieveCurrentTrolly) {
        return this.$store.getters.retrieveCurrentTrolly.cust_ref;
      } else {
        return "";
      }
    },

    async submit() {
      this.loading = true;

      if (this.$refs.createOrderForm.validate()) {
        //Check if we have an existing trolly
        if (!this.$store.getters.retrieveCurrentTrolly) {
          // We Do NOT so create one now
          let orderObject = {
            orderDate: this.orderDate,
            custRef: this.custRef
          };

          console.log(orderObject);

          let trollyCreated = await this.createNewTrolly(orderObject);

          if (trollyCreated) {
            console.log("-- Created Trolly");
            //Get our trollies next
            this.getTrollys()
              .then(response => {
                console.log("-- Got Trollys");
                console.log(response);
                this.$router.push({ name: "OrdersSearch" });
              })
              .catch(er => {
                console.log(er);
              });
          } else {
            console.log("-- Error Creating Trolly");
          }
        } else {
          //We Do
          console.log("-- Existing Trolly");

          //Check if we've changed anything
          if (
            this.$store.getters.retrieveCurrentTrolly.order_date !=
              this.orderDate ||
            this.$store.getters.retrieveCurrentTrolly.cust_ref != this.custRef
          ) {
            //We have so update our trolly here
            console.log("-- Date or Reference Amended");
            let trollyDateUpdate = true;
            let trollyRefUpdate = true;

            let orderObject = {
              orderDate: this.orderDate,
              custRef: this.custRef
            };

            if (
              this.$store.getters.retrieveCurrentTrolly.order_date !=
              this.orderDate
            ) {
              //Update our order date here
              trollyDateUpdate = await this.updateTrollyOrderDate(orderObject);
            }

            console.log("-- Comparing Trolly Ref to ours");
            console.log(this.$store.getters.retrieveCurrentTrolly.cust_ref);
            console.log(this.custRef);

            if (
              this.$store.getters.retrieveCurrentTrolly.cust_ref != this.custRef
            ) {
              //Update our cust refrerence
              trollyRefUpdate = await this.updateTrollyCustRef(orderObject);
            }

            console.log("-- Checking Data Success");
            console.log(trollyDateUpdate);
            console.log(trollyRefUpdate);

            if (trollyDateUpdate == true && trollyRefUpdate == true) {
              console.log("-- Amended Trolly");
              //Get our trollies next
              this.getTrollys()
                .then(response => {
                  console.log("-- Got Trollys");
                  console.log(response);
                  this.$router.push({ name: "OrdersSearch" });
                })
                .catch(er => {
                  console.log(er);
                });
            } else {
              console.log("-- Error Creating Trolly");
            }
          } else {
            //We have NOT so continue
            console.log("-- Nothing Amended");
            this.$router.push({ name: "OrdersSearch" });
          }
        }
      }

      this.loading = false;
    }
  }
};
</script>